import { Vue, Component, Prop } from 'vue-property-decorator'

/**
 *   
 *
 */

// uuid
let uuid: number = 0

@Component({
  name: 'mixinsBaseWidget',
  inheritAttrs: true
})
export default class mixinsBaseWidget extends Vue {
  public uuid: number = 0

  // 
  @Prop() value!: any

  // 
  @Prop({ type: String }) name!: string

  // 
  @Prop({ type: String }) type!: string

  //  key
  @Prop({ type: String }) field!: string

  // 
  @Prop({ type: String }) label!: string

  // 
  @Prop({ type: String }) alias!: string

  // 
  @Prop({ type: Number }) orderNum!: number

  // 
  @Prop({ type: [Boolean, Number, String] }) boolOrderApprove!: boolean | number | string

  // 
  @Prop({ type: [Boolean, Number, String] }) boolOrderUnique!: boolean | number | string

  // 
  @Prop({ type: [Boolean, Number, String] }) boolShowDetail!: boolean | number | string

  // 
  @Prop({ type: [Boolean, Number, String] }) boolShowList!: boolean | number | string

  // 
  @Prop({ type: [Boolean, Number, String] }) boolOrderSearch!: boolean | number | string

  // 
  @Prop({ type: String }) securityType!: string

  // 
  @Prop({ type: Array, default: () => [] }) ruleList!: Array<rule>

  // 
  @Prop({ type: Array, default: () => [] }) eventList!: Array<widgetEvent>

  // 
  @Prop({ type: Object, default: () => ({}) }) configData!: any

  private eventTypeMap: { [k: string]: string } = {}

  public emitEvent(type: string, params: any) {
    if (this.eventTypeMap[type]) {
      this.dispatch('globalPageWrapper', {
        event: this.eventTypeMap[type],
        params: params,
        level: 10
      })
    }
  }

  public created() {
    this.uuid = ++uuid
  }
}
