
















































































































































































































































































import { Vue, Component, Prop, Watch, Ref } from 'vue-property-decorator'
import { isNull, isArray, isString, isObject } from 'plugin/helper'

import components from '../../inside/components'

@Component({
  name: 'globalPageWrapper',
  components
})
export default class globalPageWrapper extends Vue {
  @Ref('globalValidate') public globalValidateRef!: globalValidate

  // 
  @Prop({ type: Object, default: () => ({}) }) productInfo!: any
  // 
  @Prop({ type: Number, default: () => '' }) pageIndex!: Number
  // 
  @Prop({ type: Array, default: () => [] }) template!: Array<any>

  public componentName: string = 'globalPageWrapper'
  private insideTemplate: Array<any> = []

  @Watch('template', { immediate: true })
  private templateHandler(): void {
    this.insideTemplate = []
    this.$nextTick(() => {
      this.insideTemplate = this.template
    })
  }

  // 
  private componentIsRegister(component: any, componentType: string) {
    const { type, configData } = component

    if (type === componentType && configData) {
      const { isHide, pageIndex } = configData

      //   
      if (!isHide && pageIndex === this.pageIndex) {
        // 
        return this.showLogicComputed(component, 1)
      }

      return false
    }

    return false
  }

  // 
  private widgetIsRegister(widget: any, widgetType: string, needIsHide: boolean = true) {
    const { type, configData } = widget

    if (type === widgetType && configData) {
      //  isHide 
      if (!needIsHide) return this.showLogicComputed(widget, 1)

      return !widget.configData.isHide && this.showLogicComputed(widget, 1)
    }

    return false
  }

  /*
   *  component showLogic 
   * type
   * 1  showLogicList 
   * 2  showLogicList 
   */
  private showLogicComputed(component: any, type: 1 | 2, form?: any): boolean {
    // 
    let show: boolean = true
    // 
    let formMap: { [k: string]: any } = form

    const showLogicList: Array<{ bind: string; value: string }> = component.configData.showLogicList

    // 
    if (isNull(showLogicList)) return show

    // 
    if (isNull(formMap)) {
      // 
      const forms = this.getValuesByComponentType('form')
      //  map
      forms.forEach(form => (formMap = { ...formMap, ...form }))
    }

    const recursion = (value: any, logicValue: string): boolean => {
      if (isArray(value) && value.includes(logicValue)) {
        return true
      }

      if (isString(value) && value === logicValue) {
        return true
      }

      return false
    }

    for (let i = 0, l = showLogicList.length; i < l; i++) {
      const logic = showLogicList[i]
      // key
      if (formMap.hasOwnProperty(logic.bind)) {
        // 
        show = recursion(formMap[logic.bind], logic.value)
        //  "", 
        if (show && type === 1) return show
        //  "", 
        if (!show && type === 2) return show
      }
    }

    return show
  }

  // 
  private clearComponentValue(component: any) {
    if (isObject(component.value)) {
      component.value = {}
    } else if (isArray(component.value)) {
      component.value = []
    } else {
      component.value = ''
    }
  }

  // 
  public clearValueByShowLogic(field: string, value: any) {
    const form = { [field]: value }

    const recursion = (components: Array<any>) => {
      components.forEach(component => {
        const { configData } = component

        if (configData) {
          const { showLogicList } = configData

          if (showLogicList) {
            const isShow = this.showLogicComputed(component, 1, form)

            //   
            if (!isShow) this.clearComponentValue(component)
          }
        }

        if (component.fieldList) recursion(component.fieldList)
      })
    }

    recursion(this.insideTemplate)
  }

  // 
  public getValuesByComponentType(type: string, excludeFieldTypeList: Array<string> = []): Array<any> {
    const values: Array<any> = []

    // 
    if (type === 'form') {
      const forms = this.insideTemplate.filter(item => item.type === 'form')

      forms.forEach(form => {
        const value: { [k: string]: any } = {}

        form.fieldList.forEach((item: any) => {
          if (!excludeFieldTypeList.includes(item.type)) {
            value[item.field] = item.value
          }
        })

        form.value = value

        values.push(value)
      })

      return values
    }

    // 
    const recursion = (components: Array<any>, type: string) => {
      components.forEach(item => {
        if (item.type === type && !isNull(item.value)) {
          values.push(item.value)
        }

        if (item.fieldList) {
          recursion(item.fieldList, type)
        }
      })
    }

    recursion(this.insideTemplate, type)

    return values
  }

  // 
  public setValuesByComponentType(type: string, value: any) {
    const recursion = (components: Array<any>, type: string) => {
      components.forEach(item => {
        if (item.type === type) {
          item.value = value
        }

        if (item.fieldList) {
          recursion(item.fieldList, type)
        }
      })
    }

    recursion(this.insideTemplate, type)
  }

  //   id
  public getProductId(): string {
    return this.productInfo.id || ''
  }

  //  id
  public getNetworkId(): string {
    if (this.productInfo && this.productInfo.networkList) {
      const network = this.getValuesByComponentType('network')[0]
      const networkList = this.productInfo.networkList.filter((item: any) => item.name === network)
      return networkList && networkList[0] ? networkList[0].id || '' : ''
    }

    return ''
  }
}
