import router from '@/router'
import store from '@/store'
import { setDocumentTitle } from 'plugin/helper'

// 
const whiteList: Array<string> = ['/403', '/video/index']

router.beforeEach(async (to, from, next) => {
  // token 
  if (store.getters['auth/token']) {
    next()
    return
  }

  //  
  if (whiteList.includes(to.path)) {
    next()
    return
  }

  // 
  try {
    // 
    await store.dispatch('auth/login')
    next()
  } catch (error) {
    // 
    await store.dispatch('auth/loginOut')
  }
})

router.afterEach(to => {
  // 
  if (to.meta.title) {
    setDocumentTitle(to.meta.title)
  }

  //   scroll.top
  setTimeout(() => {
    const scroll = to.meta.scroll

    const el = document.querySelector(scroll && scroll.el ? scroll.el : '.layout-main')

    if (el) {
      el.scrollTo({ top: scroll && scroll.top ? scroll.top : 0 })
    }
  }, 0)
})
