import { Vue, Component, Prop, Watch, InjectReactive } from 'vue-property-decorator'
import { isNull } from 'plugin/helper'
import { cloneDeep } from 'lodash'

@Component({
  name: 'mixinsBaseField'
})
export default class mixinsBaseField extends Vue {
  @InjectReactive({ from: 'labelPosition', default: 'left' }) labelPosition!: string

  @Prop() value!: any

  @Prop({ type: String }) formatValue!: string

  @Prop({ type: String }) label!: string

  @Prop({ type: Object, default: () => ({}) }) configData!: any

  public insideValue: any = this.value

  /**
   * 
   */
  // 
  public get configType(): string {
    return this.configData.type
  }

  // 
  public get configLabel(): string {
    return this.label || this.configData.label
  }

  // 
  public get configLabelWidth(): string | number {
    return this.configData.labelWidth
  }

  // 
  public get configSize(): string {
    return this.configData.size || 'large'
  }

  // 
  public get configPlaceholder(): string {
    return this.configData.placeholder
  }

  // 
  public get configDisabled(): boolean {
    return this.configData.disabled
  }

  // 
  public get configReadonly(): boolean {
    return this.configData.readonly
  }

  // 
  public get configMaxlength(): number {
    return this.configData.maxlength
  }

  // 
  public get configTitle(): string {
    return this.configData.title
  }

  //   options  option 
  public get configOptions(): Array<any> {
    return this.configData.options || this.configData.option || []
  }

  // 
  public get configRows(): number | string {
    return this.configData.rows
  }

  // 
  public get configAutosize(): number | string {
    return this.configData.autosize
  }

  // 
  public get configMinDate(): string {
    return this.configData.minDate
  }

  // 
  public get configMaxDate(): string {
    return this.configData.maxDate
  }

  /**
   * 
   */
  public get configIsNeedExample() {
    return this.configData.isNeedExample || false
  }

  public get configExampleTitle() {
    return this.configData.exampleTitle || ''
  }

  public get configExampleDescription() {
    return this.configData.exampleDescription || ''
  }

  public get configExampleImageUrl() {
    if (this.configData.exampleImageUrl) {
      return this.$general.showBase64(this.configData.exampleImageUrl)
    }
    return ''
  }

  // label  
  public get labelPositionIsTop() {
    return this.labelPosition === 'top'
  }

  @Watch('value', { immediate: true })
  public valueHandler() {
    this.insideValue = this.value
  }

  public updateValue(value: any) {
    this.$emit('input', cloneDeep(value))
  }

  public changeValue(value: any) {
    this.$emit('change', cloneDeep(value))
  }

  public updateFormatValue(value: string) {
    this.$emit('update:format-value', value)
  }

  public created() {
    this.$on('field-set-value', (value: any) => {
      this.insideValue = value
    })
  }

  public beforeDestroy() {
    this.$off('field-set-value')
  }

  //  globalPageWrapper 
  public globalPageWrapperRef() {
    const globalPageWrapperList = this.findComponentByName('globalPageWrapper', this.$root, 10, 'single')

    if (!isNull(globalPageWrapperList)) {
      return globalPageWrapperList[0] as globalPageWrapper
    }
  }

  floatMenuToggle(state: boolean) {
    this.dispatch('floatMenu', {
      event: 'floatMenu.toggle',
      params: [state],
      level: 10
    })
  }
}
